CA,cAAA;EACA,YAAA;EACA,uBAAA;AA1CJ;AAiDE;EAxBF;IAyBI,cAAA;IACA,eAAA;EA9CF;AACF;AAgDE;EACE,2BAAA;EACA,0CAAA;EACA,+BAAA;AA9CJ;;AAkDA;EACE,mBAAA;EACA,0BAAA;EACA,4BAAA;EAEA,eAAA;EACA,0BAAA;AAhDF;;AAoDA;EACE,kBAAA;EACA,yBAAA;EACA,iBAAA;EACA,WAAA;EACA,kBAAA;EACA,kBAAA;AAjDF;AAmDE;EACE,kBAAA;EACA,cA/cS;EAgdT,gCAAA;AAjDJ;AAoDE;EACE,aAAA;EACA,gCAAA;AAlDJ;;AAsDA;EACE,aAAA;EACA,WAAA;EACA,gBAAA;EACA,0BAAA;EACA,gGAAA;AAnDF;;AAuDE;EACE,aAAA;EACA,kBAAA;AApDJ;AAsDI;EACE,iBAAA;EACA,kBAAA;EACA,cAzeO;AAqbb;AAuDI;EACE,iBAAA;EACA,gCAAA;EACA,gBAAA;EACA,kBAAA;AArDN;;AA2DA;EACE,sBAAA;EACA,2BAAA;EACA,sBAAA;EACA,WAAA;EACA,SAAA;EACA,sBAAA;EACA,kBAAA;EACA,eAAA;EACA,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,uBAAA;AAxDF;AA2DE;EACE,qBAAA;AAzDJ;AA4DE;EACE,gBAAA;EACA,YAAA;EACA,2CAAA;AA1DJ;;AA8DA;EACE,kBAAA;EACA,QAAA;EACA,SAAA;EACA,gCAAA;EACA,WAAA;EACA,YAAA;EACA,oCAAA;EACA,kBAAA;EACA,YAAA;EACA,eAAA;EACA,aAAA;EACA,mBAAA;EACA,uBAAA;EACA,+BAAA;AA3DF;;AA8DA;EACE,WAAA;AA3DF;AA6DE;EACE,WAAA;EACA,YAAA;EACA,cAAA;AA3DJ;;AAiEA;EACE,+DAAA;EACA,eAAA;EACA,cAAA;EACA,iBAAA;EACA,+BAAA;EACA,kBAjjBc;AAmfhB;AAgEE;EACE,gBAAA;AA9DJ","sourcesContent":["\n@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&family=VT323&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Silkscreen:wght@400;700&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&family=Roboto+Condensed:wght@400;700&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');\n\n\n// Font mixins\n@mixin roboto-regular {\n  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n}\n\n@mixin roboto-condensed {\n  font-family: 'Roboto Condensed', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n}\n\n@mixin roboto-light {\n  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 300;\n  -webkit-font-smoothing: antialiased;\n}\n\n\n@mixin robotomono-light {\n  font-family: 'RobotoMono', -apple-system, monospace;\n  font-weight: 300;\n  -webkit-font-smoothing: none;\n}\n\n\n// Font imports\n@font-face {\n  font-family: 'alagard';\n  src: url('../fonts/alagard.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n}\n\n@font-face {\n  font-family: 'monaco';\n  src: url('../fonts/monaco.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n\n}\n\n@font-face {\n  font-family: 'RobotoMono';\n  src: url('../fonts/RobotoMono-Light.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n\n}\n\n// TODO: https://stackoverflow.com/questions/12562631/web-fonts-and-providing-fallback-fonts\n","@use \"sass:color\";\n@use 'sass:math' as math;\n@use 'fonts' as *;\n\n\n\n// Variables\n$header-font: 'Silkscreen', 'Press Start 2P', monospace;\n$body-font: 'Silkscreen', 'monaco';\n$description-font:  'Roboto Condensed', -apple-system, BlinkMacSystemFont, sans-serif;\n$accent-color: #9eff4a; // Adjusted to a softer yellow-green that matches the monster\n\n$text-light: #FFFFFF;\n$card-bg: rgba(0, 0, 0, 0.45);\n$border-radius: 8px;\n$page-bg: #2b222a;\n\n// Base styles\nhtml {\n  font-size: 16px;\n  min-width: 100vw;\n  margin: 0;\n  padding: 0;\n  background-color: $page-bg;\n\n  text-rendering: optimizeLegibility;\n  -webkit-font-smoothing: antialiased;\n  overflow-x: hidden; /* Prevent horizontal scrolling */\n}\n\nbody {\n  margin: 0;\n  padding: 0;\n  font-size: clamp(2rem, 5vw, 4rem);\n  line-height: 1.0;\n  font-family: $body-font;\n  font-weight: normal;\n  color: $text-light;\n\n  //max-width: 1200px;\n  //margin: 25px auto;\n  //padding: 0 20px;\n\n  letter-spacing: 0rem;\n  width: 100%; /* Explicit width */\n  max-width: 100vw; /* Don't exceed viewport */\n  overflow-x: hidden; /* Hide horizontal overflow */\n  box-sizing: border-box; /* Include padding in width calculation */\n}\n\n// Typography\nh1, h2, h3, h4, h5, h6 {\n  line-height: 1;\n  text-rendering: geometricPrecision;\n  font-smooth: never;\n  -webkit-font-smoothing: none;\n  margin-top: 0;\n  margin-bottom: 2rem;\n  font-weight: normal;\n  color: $text-light;\n  letter-spacing: 0rem;\n}\n\nh1, h2, h3 {\n  font-family: $header-font;\n  @include roboto-light;\n  font-size: 64px;\n  line-height: 1.6;\n  color: #f0f0f0;\n\n  // Create appropriate spacing consistent with your design\n  margin: 2em 0;\n\n  // Slightly increase letter-spacing for better readability on dark backgrounds\n  letter-spacing: 0.2px;\n}\n\nh4, h5, h6 {\n  font-family: $description-font;\n  @include roboto-light;\n  font-size: 32px;\n  line-height: 1.6;\n  color: #f0f0f0;\n\n  // Create appropriate spacing consistent with your design\n  margin: 2em 0;\n\n  // Slightly increase letter-spacing for better readability on dark backgrounds\n  letter-spacing: 0.2px;\n}\n\n//\n//h1 { font-size: 6rem; line-height: 1.0; letter-spacing: 0rem; }\n//h2 { font-size: 6rem; line-height: 1.0; letter-spacing: 0rem; }\n//h3 { font-size: 5rem; line-height: 1.0; letter-spacing: 0rem; }\n//h4 { font-size: 5rem; line-height: 1.0; letter-spacing: 0rem; }\n//h5 { font-size: 3rem; line-height: 1.0; letter-spacing: 0rem; }\n//h6 { font-size: 3rem; line-height: 1.0; letter-spacing: 0rem; }\n\n\n// Adjust content container width for larger screens\nbody {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n\n  max-width: 2400px; // Increased to match gallery container\n  margin: 25px auto;\n  padding: 0 30px; // Increased padding for larger screens\n\n\n  // Create a proper grid layout\n  @media (min-width: 2001px) {\n    max-width: 2400px;\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) {\n    max-width: 1400px;\n  }\n\n  @media (min-width: 769px) and (max-width: 1500px) {\n    max-width: 800px;\n  }\n\n  @media (max-width: 768px) {\n    max-width: 700px;\n    padding: 0 15px;\n  }\n}\n\n\nheader {\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  width: 100%;\n  max-width: 100vw;\n  box-sizing: border-box;\n  overflow: hidden;\n}\nfooter {\n  background: $card-bg;\n  padding: 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n  width: 100%;\n  max-width: 100vw;\n  box-sizing: border-box;\n  overflow: hidden;\n}\n\n// Links\na {\n  font-weight: normal;\n  text-decoration: none;\n  color: $accent-color;\n\n  &:focus {\n    outline: thin dotted;\n    color: color.adjust($accent-color, $lightness: 10%);\n  }\n}\n\n// Marketing section styling\n.marketing-content {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n  //background: $card-bg;\n  padding: 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n}\n\n.tagline {\n  @include roboto-light;\n  line-height: 0.0;\n  margin-bottom: 20px;\n  color: $accent-color;\n}\n\n.cta-button {\n  margin-top: 20px;\n  padding: 10px 20px;\n  background-color: $accent-color;\n  color: #000;\n  border-radius: 4px;\n  display: inline-block;\n  transition: background-color 0.3s ease;\n\n  &:hover {\n    background-color: color.adjust($accent-color, $lightness: -20%);\n  }\n}\n\n\n/* Keep your original CSS exactly as is */\n.cropped-image {\n  margin: 0px;\n  padding: 0px;\n  width: 100%; /* Container width */\n  height: 300px; /* Fixed height */\n  overflow: hidden; /* Hide parts outside container */\n  /* NO position: relative here */\n  //position: relative;\n\n  /* Safari-specific orientation change fix */\n  @supports (-webkit-touch-callout: none) {\n    width: 100vw; /* Use viewport width instead of percentage */\n    margin-left: calc(-50vw + 50%); /* Center relative to the body */\n    box-sizing: border-box;\n  }\n}\n\n.cropped-image img {\n  image-rendering: pixelated;\n  image-rendering: -moz-crisp-edges;\n  image-rendering: crisp-edges;\n  -ms-interpolation-mode: nearest-neighbor; /* IE */\n  /* Center the image horizontally */\n  position: absolute;\n  left: 50%;\n  transform: translateX(-50%);\n  /* Optional: adjust vertical position */\n  top: 0;\n  max-width: none;     /* IMPORTANT: Prevents the image from resizing to fit container */\n  //height: 100%;\n  //width: auto;\n  //\n  /* Set minimum width to ensure coverage on large screens */\n  min-width: 100vw; /* Always fill viewport width */\n  height: auto; /* Let height adjust based on aspect ratio */\n  min-height: 100%; /* Make sure it fills the container height */\n\n  /* Position from the top for better framing */\n  top: 0;\n  z-index: -1; /* Place behind all other content */\n\n  /* Add object-fit as a fallback for browsers that support it */\n  object-fit: cover;\n  object-position: center top;\n\n  /* Add Safari-specific orientation change fix */\n  @supports (-webkit-touch-callout: none) {\n    /* Ensure image covers the full width during rotation */\n    min-width: 100vw;\n    object-fit: cover;\n    object-position: top center;\n  }\n}\n\n\n/* Add media query for extra large displays */\n@media (min-width: 2400px) {\n  .cropped-image img {\n    /* Scale up for extremely large monitors */\n    min-width: 100%; /* Use container width */\n    width: 100vw; /* Ensure viewport coverage */\n    max-width: none; /* Override max-width limitations */\n  }\n}\n\n/* Add only these Safari-specific fixes */\n@supports (-webkit-touch-callout: none) {\n  /* Only fix the container horizontal overflow in Safari */\n  header, #header {\n    //max-width: 100vw;\n    //overflow-x: hidden;\n  }\n}\n\n.banner-container {\n  position: absolute;\n  top: 85%;\n  margin: 0px;\n  padding: 0px;\n  //width: 100px; /* Container width */\n  width: 100%;\n  //max-width: 2400px; // Increased for large monitors\n  margin: 0 auto;\n}\n\n/* Refactor your banner-image class in main.scss */\n.banner-image {\n  /* Keep existing pixel-perfect rendering properties */\n  image-rendering: pixelated;\n  image-rendering: -moz-crisp-edges;\n  image-rendering: crisp-edges;\n  -ms-interpolation-mode: nearest-neighbor; /* IE */\n\n  /* Center the image */\n  display: block;\n  margin: 0 auto;\n\n  /* Don't set max-width to allow for exact integer scaling */\n  /* max-width: 100%; */ /* Remove this line */\n  height: auto;\n\n  /* Keep transform origin */\n  transform-origin: center;\n\n  /* Keep your original styles */\n  border-radius: $border-radius $border-radius 0 0;\n\n  /* Add Safari-specific rendering */\n  @supports (-webkit-touch-callout: none) {\n    transform: translateZ(0); /* Helps with Safari rendering */\n  }\n}\n\n.image-placeholder {\n  //margin-top: calc(100vh * 0.3552 + 47.6051px);\n  margin-bottom: 450px; /* More specific and forceful for testing */\n\n  @media (min-width: 2001px) { // 4 items per row for very large screens\n    margin-bottom: 1850px;\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) { // 3 items per row\n    margin-bottom: 1650px;\n  }\n\n  @media (min-width: 769px) and (max-width: 1500px) { // 2 items per row\n    margin-bottom: 1650px;\n  }\n\n  @media (max-width: 768px) {\n    margin-bottom: 425px;\n  }\n\n}\n\n// Content sections\n.content {\n  background: $card-bg;\n  padding: 25px 25px 25px 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.video-background {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  width: 50vw;\n}\n\n#fullpage {\n  display: none;\n  position: fixed;\n  z-index: 9998;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  background-color: rgba(0, 0, 0, 0.85); /* Add overlay background */\n  opacity: 0;\n  transition: opacity 0.3s ease-in-out;\n\n  .fullpage-image {\n    width: 90%; /* Reduced from 100% to create margin */\n    height: 90%; /* Reduced from 100% to create margin */\n    background-size: contain;\n    background-repeat: no-repeat;\n    background-position: center center;\n    background-color: transparent;\n    image-rendering: pixelated;\n    image-rendering: crisp-edges;\n    //transform: scale(1); /* Integer scaling for pixel art */\n    transform-origin: center center;\n    margin: 5vh auto; /* Add vertical and horizontal margin */\n  }\n}\n\n// Gallery styles\n.gallery-container {\n  width: 100%;\n  //max-width: 2400px; // Increased for large monitors\n  margin: 0 auto;\n}\n\n.artwork-grid {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 20px;\n  justify-content: center;\n\n  // Create a proper grid layout\n  @media (min-width: 2001px) { // 4 items per row for very large screens\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(25% - 15px);\n      max-width: calc(25% - 15px);\n    }\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) { // 3 items per row\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(33.333% - 14px);\n      max-width: calc(33.333% - 14px);\n    }\n  }\n\n  @media (min-width: 764px) and (max-width: 1500px) { // 2 items per row\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(50% - 10px);\n      max-width: calc(50% - 10px);\n    }\n  }\n}\n\n.artwork-item {\n  border-radius: $border-radius;\n  box-shadow: 0 4px 12px color.adjust($card-bg, $alpha: -0.5);\n  transition: transform 0.3s ease, box-shadow 0.3s ease;\n  overflow: hidden;\n  margin-bottom: 20px;\n  position: relative;\n  display: inline-block;\n\n  img {\n    object-fit: contain;\n    width: 100%;\n    image-rendering: pixelated;\n    image-rendering: crisp-edges;\n    border-radius: $border-radius $border-radius 0 0;\n    display: block;\n    height: auto;\n    aspect-ratio: 1920/1080;\n    //@media (max-width: 768px) {\n    //  height: 300px;\n    //}\n  }\n\n  // Default (mobile) styling\n  @media (max-width: 768px) {\n    flex: 0 0 100%;\n    max-width: